import { declension } from "../../utils/declension";

export const getWeightText = weight => {
  if (weight === 0 || !weight) {
    return "   ";
  } else if (weight > 0) {
    const time = declension("", "", "", weight);

    return `   ${weight} ${time}  `;
  } else {
    const absWeight = Math.abs(weight);
    const time = declension("", "", "", absWeight);

    return `   ${absWeight} ${time}  `;
  }
};
